<template>
  <div>
    <template v-if="items.length > 0">
      <b-table
        v-if="!loading"
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        responsive
        :items="items"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        @filtered="onFiltered"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        class="mt-3"
        id="invoices-table"
      >
        <template v-slot:cell(username)="data">{{
          data.item.user.username ? data.item.user.username : undefined
        }}</template>
        <template v-slot:cell(name)="data">{{
          data.item.user.name ? data.item.user.name : undefined
        }}</template>
        <template v-slot:cell(last_name)="data">{{
          data.item.user.last_name ? data.item.user.last_name : undefined
        }}</template>
        <template v-slot:cell(address)="data">{{
          data.item.user.address ? data.item.user.address : undefined
        }}</template>
        <template v-slot:cell(country)="data">{{
          data.item.user.country ? data.item.user.country : undefined
        }}</template>
        <template v-slot:cell(city)="data">{{
          data.item.user.city ? data.item.user.city : undefined
        }}</template>
        <template v-slot:cell(document)="data"
          >{{ data.item.user.document ? data.item.user.document : undefined }}
        </template>

        <template v-slot:cell(email)="data">{{
          data.item.user.email ? data.item.user.email : undefined
        }}</template>
        <template v-slot:cell(phone)="data">{{
          data.item.user.phone ? data.item.user.phone : undefined
        }}</template>

        <template v-slot:cell(value)="data">
          <p>${{ data.item.plan.price }} USD</p>
        </template>

        <template v-slot:cell(fees)="data">
          <p>${{ data.item.plan.fees }} USD</p>
        </template>
        <template v-slot:cell(plan_name)="data">
          <p>{{ data.item.plan.name }} USD</p>
        </template>
        <template v-slot:cell(currency)="data">
          <p>{{ data.item.currency.name }}</p>
        </template>
        <template v-slot:cell(txid)="data">
          <p>{{ data.item.txid }}</p>
        </template>

        <template v-slot:cell(wallet)="data">
          <p>{{ data.item.wallet }}</p>
        </template>

        <template v-slot:cell(corp_activation)="data">
          <span v-if="data.item.corp_activation" class="badge badge-success"
            >✓</span
          >
        </template>
      </b-table>
      <nav class="print-action-invoices">
        <paginate
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </template>
  </div>
</template>

<script>
import Paginate from "vuejs-paginate";
import _ from "lodash";
import * as table2csv from "table2csv";

export default {
  name: "invoices-report",
  props: {
    hover: {
      type: Boolean,
      default: true,
    },
    striped: {
      type: Boolean,
      default: true,
    },
    bordered: {
      type: Boolean,
      default: true,
    },
    small: {
      type: Boolean,
      default: true,
    },
    fixed: {
      type: Boolean,
      default: false,
    },
    perPage: {
      type: Number,
      default: 0,
    },
    fromDate: {
      type: String,
      default: "",
    },
    toDate: {
      type: String,
      default: "",
    },
    selectedUsername: {
      type: String,
      default: "",
    },
  },
  components: {
    Paginate,
  },
  watch: {
    loading(newVal) {
      this.$emit("component-loading", newVal);
    },
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    },
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID" },
        { key: "username", sortable: true, label: "username" },
        {
          key: "value",
          sortable: true,
          label: "value",
        },
        {
          key: "fees",
          sortable: true,
          label: "fees",
        },
        {
          key: "total_crypto",
          sortable: true,
          label: "total crypto",
        },
        {
          key: "plan_name",
          sortable: true,
          label: "plan name",
        },
        {
          key: "currency",
          sortable: true,
          label: "currency",
        },
        {
          key: "corp_activation",
          sortable: true,
          label: "corp activation",
          tdClass: "text-center",
        },
        {
          key: "txid",
          sortable: true,
          label: "txid",
        },
        {
          key: "wallet",
          sortable: true,
          label: "wallet",
        },
        {
          key: "name",
          sortable: true,
          label: "name",
        },
        {
          key: "last_name",
          sortable: true,
          label: "last name",
        },
        {
          key: "email",
          sortable: true,
          label: "email",
        },
        {
          key: "document",
          sortable: true,
          label: "document",
        },
        {
          key: "country",
          sortable: true,
          label: "country",
        },
        {
          key: "city",
          sortable: true,
          label: "city",
        },
        {
          key: "address",
          sortable: true,
          label: "address",
        },
        {
          key: "phone",
          sortable: true,
          label: "phone",
        },
        {
          key: "payment_confirmed_at",
          sortable: true,
          label: "payment confirmed at",
        },
        {
          key: "created_at",
          sortable: true,
          label: "created at",
        },
      ],
      currentPage: 1,
      totalItems: 0,
      pageCount: 1,
      totalRows: 0,
      loaders: {},
      show: true,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
    };
  },
  created() {
    window.onafterprint = () => {
      this.printTable(false, "");
    };
  },
  methods: {
    nextPageCall(pageNum) {
      this.listReports(pageNum);
    },
    printTable(print, myClass) {
      var cols = document.getElementsByClassName("print-action-invoices");
      Array.prototype.forEach.call(cols, function(el) {
        el.style.display = myClass;
      });

      if (print) window.print();
    },
    listReports(pageNo = 1) {
      this.loading = true;
      let params = "";
      if (this.fromDate && this.toDate) {
        params = `&from_date=${this.fromDate}&to_date=${this.toDate}`;
      }
      this.$http(
        "/api/v1/admin/reports/users?per_page=" +
          this.perPage +
          "&page=" +
          pageNo +
          params,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken,
          },
        }
      )
        .then((response) => {
          this.items = response.data;
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    csvExport() {
      $("#invoices-table").table2csv();
    },
  },
};
</script>
