<template>
  <div>
    <div class="card">
      <div class="card-body">
        <h3>Total: ${{ totalRewards }}</h3>
        <h5 class="text-muted">From: {{ fromDate }} To: {{ toDate }}</h5>
      </div>
    </div>
    <template v-if="items.length > 0">
      <b-table
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        responsive
        :items="items"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        @filtered="onFiltered"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        class="mt-3"
        id="rewards-table"
      >
        <template v-slot:cell(referred_user)="data">{{
          data.item.referred_user ? data.item.referred_user : ""
        }}</template>
      </b-table>
    </template>
  </div>
</template>

<script>
import Paginate from "vuejs-paginate";
import _ from "lodash";
import * as table2csv from "table2csv";

export default {
  name: "invoices-report",
  props: {
    hover: {
      type: Boolean,
      default: true,
    },
    striped: {
      type: Boolean,
      default: true,
    },
    bordered: {
      type: Boolean,
      default: true,
    },
    small: {
      type: Boolean,
      default: true,
    },
    fixed: {
      type: Boolean,
      default: false,
    },
    fromDate: {
      type: String,
      default: "",
    },
    toDate: {
      type: String,
      default: "",
    },
    selectedUsername: {
      type: String,
      default: "",
    },
    selectedRewardTypes: {
      type: Array,
      default: "",
    },
  },
  components: {
    Paginate,
  },
  watch: {
    loading(newVal) {
      this.$emit("component-loading", newVal);
    },
  },
  computed: {
    propDateRange() {
      return this.fromDate && this.toDate
        ? `&from_date=${this.fromDate}&to_date=${this.toDate}`
        : "";
    },
    propUsername() {
      return this.selectedUsername ? `&username=${this.selectedUsername}` : "";
    },
    propRewardTypes() {
      return this.selectedRewardTypes.length > 0
        ? `&reward_types=${this.selectedRewardTypes.toString()}`
        : "";
    },
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID" },
        {
          key: "value",
          sortable: true,
          label: "value",
        },
        {
          key: "reward_type",
          sortable: true,
          label: "reward_type",
        },
        {
          key: "reward_status",
          sortable: true,
          label: "reward_status",
        },
        {
          key: "referred_user",
          sortable: true,
          label: "referred_user",
        },
        {
          key: "redeemed_binary_points",
          sortable: true,
          label: "redeemed_binary_points",
          tdClass: "text-center",
        },
        {
          key: "created_at",
          sortable: true,
          label: "created_at",
        },
      ],
      currentPage: 1,
      totalItems: 0,
      pageCount: 1,
      totalRows: 0,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      perPage: 10000000,
      totalRewards: 0,
    };
  },
  created() {
    window.onafterprint = () => {
      this.printTable(false, "");
    };
  },
  methods: {
    nextPageCall(pageNum) {
      this.listReports(pageNum);
    },
    printTable(print, myClass) {
      var cols = document.getElementsByClassName("print-action-invoices");
      Array.prototype.forEach.call(cols, function(el) {
        el.style.display = myClass;
      });

      if (print) window.print();
    },
    listReports(pageNo = 1) {
      this.loading = true;
      this.$http(
        "/api/v1/admin/reports/rewards?per_page=" +
          this.perPage +
          "&page=" +
          pageNo +
          this.propDateRange +
          this.propUsername +
          this.propRewardTypes,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken,
          },
        }
      )
        .then((response) => {
          this.items = response.data;
          this.totalRewards = response.data.length > 0 && this.propUsername && response.data[0].total || response.data.total || 0;
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );
          this.loading = false;
        })
        .catch((error) => {
          console.log(error);
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    csvExport() {
      $("#rewards-table").table2csv();
    },
  },
};
</script>
